import React from 'react';
import { connect } from 'react-redux';
import { fetchProjects } from '../../actions'
import Header from '../Header/Header';
import ImageGrid from '../AdminWindow/ImageGrid/ImageGrid';

const formProps = {
    gridTitle: 'Portfolio',
    gridCardButton: 'Open App'
}

class Portfolio extends React.Component {
    
    componentDidMount = () => {
        this.props.fetchProjects();
    }
    
    render() {
        console.log(this.props);
        return(
            <div>
                <Header />
                <ImageGrid formProps={formProps} projects={this.props.projects} />
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return { projects: Object.values(state.projects) }
}

export default connect(mapStateToProps, { fetchProjects })(Portfolio);