import React from 'react';
import ImageGrid from '../ImageGrid/ImageGrid';
import { connect } from 'react-redux';
import { fetchProjects, deleteProject } from '../../../actions';


const formProps = {
    gridTitle: 'Select a Project to Delete',
    gridCardButton: 'Delete',
    modalActionLabel: `Delete - ...`,
    modalActionMessage: `Are you sure you want to delete ...?`,
    modalActionDoneMessage: `... Has been deleted`
}

class ProjectDelete extends React.Component {

    componentDidMount() {
        this.props.fetchProjects();
    }

    onDeleteClick = (project) => {
        this.props.deleteProject(project.id);
    }
    
    render() {
        return(
            <div>
                <ImageGrid id="deleteproject-image-grid" onDeleteClick={this.onDeleteClick} formProps={formProps} projects={this.props.projects} />
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return { projects: Object.values(state.projects) }
}

export default connect(mapStateToProps, { fetchProjects, deleteProject })(ProjectDelete);