import React from 'react';
import ProjectForm from '../ProjectForm/ProjectForm';
import { reduxForm } from 'redux-form';
import { createProject } from '../../../actions';
import { connect } from 'react-redux';
import { change } from 'redux-form';

const formProps = {
    title: 'Create a Project',
    label_1: 'Project Name',
    label_2: 'Description',
    button: 'Add Project'
}

class ProjectCreate extends React.Component {

    renderInput = ({ input }) => {

        if (input.name === 'projectName') {
            return(
                <div className="field">
                    <label>{formProps.label_1}</label>
                    <input {...input} />
                </div>
            );
        }

        if (input.name === 'projectDescription') {
            return(
                <div className="field">
                    <label>{formProps.label_2}</label>
                    <textarea {...input} />
                </div>
            );
        }

        if (input.name === 'img') {
            return <input style={{ display: 'none' }} {...input} />;
        }
    }

    onImageUpload = (ImgEnc) => {
        this.props.dispatch(change('projectCreate','img', ImgEnc));
    }

    onSubmit = (formValues) => {
        this.props.createProject(formValues);
        this.props.dispatch(change('projectCreate','projectName',''));
        this.props.dispatch(change('projectCreate','projectDescription',''));
        document.querySelector('#submittedImage').remove();
    }

    render() {
        return(
            <div>
                <form encType="multipart/form-data" className="ui form" onSubmit={this.props.handleSubmit(this.onSubmit)}>
                    <h2 style={{paddingTop: '30px'}}>{formProps.title}</h2>
                    <ProjectForm onImageUpload={this.onImageUpload} renderInput={this.renderInput} formProps={formProps} />
                </form>
            </div>
        );
    }
}
//hola

export default connect(null, { createProject })(reduxForm({ form: 'projectCreate' })(ProjectCreate));