import './AdminWindow.css';
import React from 'react';
import Logo from '../../media/logo.png';
import ProjectCreate from './ProjectCreate/ProjectCreate';
import ProjectEdit from './ProjectEdit/ProjectEdit';
import ProjectDelete from './ProjectDelete/ProjectDelete';
import { Link } from 'react-router-dom';

class AdminWindow extends React.Component {
    
    renderAdmin = () => {
        const page = window.location.pathname;
        if (page === '/admin/project-add') {
            return <ProjectCreate />;
        } else if (page === '/admin/project-edit') {
            return <ProjectEdit />;
        } else if (page === '/admin/project-delete') {
            return <ProjectDelete />;
        } else {
            return <ProjectCreate />;
        }
    }
    
    render() {
        const login = () => {
            const username = prompt("Username","");
            const password = prompt("Password","");
            if(username === "admin" && password === "admin") {
                return(
                    <div>
                        <div className="ui left fixed vertical menu">
                            <div className="item">
                                <div className="admin-menu">
                                    <img className="ui mini image" src={Logo} alt="gsr-logo" />
                                    <div id="title">
                                        <h4 className="header">Admin - Menu</h4>
                                    </div>
                                </div>
                            </div>
                            <Link to="/admin/project-add">
                                <div className="menu-item clickable">
                                    Create Project
                                </div>
                            </Link>   
                            <Link to="/admin/project-edit">
                                <div className="menu-item clickable">
                                    Edit Project
                                </div>
                            </Link>
                            <Link to="/admin/project-delete">
                                <div className="menu-item clickable">
                                    Delete Project
                                </div>
                            </Link>
                        </div>
                        <div className="option-container">
                            {this.renderAdmin()}
                        </div>
                    </div>
                );
            } else {
                login();
            }
        }
        return(login());
    }
}

export default AdminWindow;