import './ImageGrid.css';
import React from 'react';

class ImageGrid extends React.Component {
    
    renderProjects = () => {
        return this.props.projects.map((project) => {
            return(
                    <div style={{margin: '10px'}} className="ui card column column-item">
                    <div className="content-container">
                        <h3>
                            {project.projectName}
                        </h3>
                    </div>
                    <div className="image-container">
                        <div>
                            <img id={`project-logo${project.id}`} key={project.id} src={project.img} />
                        </div>
                    </div> 
                    <div className="content content-container">
                        {
                            (this.props.formProps.gridTitle === 'Portfolio') ? <div className="description"><p>{project.projectDescription}</p></div> : null
                        }
                    </div>
                    {
                        (!this.props.formProps.gridCardButton) 
                        ? null
                        : <div className="button-container">
                            <div 
                                style={{margin: '0px'}} 
                                className="ui button" 
                                onClick={() => {
                                            (this.props.formProps.gridCardButton === 'Edit') 
                                            ? this.props.onEditClick(project) 
                                            : this.props.onDeleteClick(project) 
                                                }
                                        }
                                style={{background: '#f0db4f', border: '1px solid lightgrey'}}
                            >{this.props.formProps.gridCardButton}</div>
                        </div> 
                    }
                </div>
            );
        });
    }

    render() {
        return(
            <div className="grid-container">
                <div style={{paddingBottom: '50px' , textAlign: 'center'}}><h1>{this.props.formProps.gridTitle}</h1></div>
                <div className="ui stackable six column grid">
                    {this.renderProjects()}
                </div>
            </div>
        );
    }
}

export default ImageGrid;