import React from 'react';
import { Field } from 'redux-form';
import ImageUpload from '../ImageUpload/ImageUpload';

const ProjectForm = (props) => {
    return(
        <React.Fragment>
            <Field name="projectName" component={props.renderInput} />
            <Field name="projectDescription" component={props.renderInput} />
            <div className="field">
                <label>Project Image</label>
                <div className="ui card">
                    <div className="content">
                        {
                            (props.showForm) ? props.actImg() : null
                        }
                        <ImageUpload onImageUpload={props.onImageUpload} />
                        <Field name="img" component={props.renderInput} />
                    </div>
                </div>
            </div>
            <button className="ui submit button">{props.formProps.button}</button>
        </React.Fragment>
    );
}

export default ProjectForm;