import React from 'react';
import ImageUploading from "react-images-uploading";
// { ImageUploadingPropsType, ImageListType, ImageType } is type for typescript

const maxNumber = 1;
const maxMbFileSize = 5 * 1024 * 1024; // 5Mb

class ImageUpload extends React.Component {

    onChange = (imageList) => {
        // data for submit
        this.props.onImageUpload(imageList[0].dataURL);
    };

    onError = (errors, files) => {
        console.log(errors, files);
    };
    
    render() {
        return (
            <ImageUploading
            onChange={this.onChange}
            maxNumber={maxNumber}
            multiple
            maxFileSize={maxMbFileSize}
            acceptType={["jpg", "gif", "png"]}
            onError={this.onError}
            >
            {({ imageList, onImageUpload }) => (
                // write your building UI
                <div>
                {imageList.map((image) => (
                    <div key={image.key}>
                        <div>
                            <img id="submittedImage" style={{width: '100%', maxWidth: '160px'}} src={image.dataURL} />
                        </div>
                        <div onClick={ (document.querySelector('#submittedImageEdit')) ? document.querySelector('#submittedImageEdit').remove() : null }>
                            <div
                                className="ui primary button" 
                                style={{ marginTop: '10px' }} 
                                onClick={image.onUpdate}
                            >Update</div>
                        </div>
                    </div>
                ))}
                <div id="img-upload-button" style={{ marginTop: '10px' }} className="ui primary button" onClick={() => {
                    onImageUpload();
                    let btn = document.querySelector('#img-upload-button');
                    btn.setAttribute("style", "display: none" );
                }}>Upload</div>
                </div>
            )}
            </ImageUploading>
        );
    }
}

export default ImageUpload;