import './Header.css'
import React from 'react';
import { Link } from 'react-router-dom'
import logo from '../../media/logo.png';

const Header = () => {
    return(
        <header className="ui stackable menu header-component">
                <Link to="/" className="header" id="logo">
                    <img className="page-images" src={logo} alt="gsr-logo" />
                    <div className="content header-title-container">
                        <div>GUSTAVO RIOS</div>
                        <div>{"{fullStack webDeveloper}"}</div>
                    </div>
                </Link>
                <div className="right menu">
                    <Link to="/" className="item">Portfolio</Link>
                    <Link to="/about" className="item">About me</Link>
                    <Link to="/resume" className="item">Resume</Link>
                </div>
        </header>
    );
}

export default Header;